export enum TypographyVariants {
  HERO = 'hero',
  HEADER1 = 'header1',
  HEADER2 = 'header2',
  HEADER3 = 'header3',
  HEADER4 = 'header4',
  BODY = 'body',
  LABEL = 'label',
  SMALL = 'small',
}
export enum Breakpoints {
  MOBILE = 'mobile',
  MOBILE_L = 'mobileL',
  TABLET = 'tablet',
  LAPTOPS = 'laptopS',
  LAPTOP_M = 'laptopM',
  LAPTOP_L = 'laptopL',
  DESKTOP = 'desktop',
  LARGE = 'large',
}

export type TypographyEntity = {
  fontSize: string;
  lineHeight: string;
  defaultWeight: string;
  weights: Record<string, string>;
  breakpoints: Array<{
    from: `${Breakpoints}`;
    fontSize: string;
    lineHeight: string;
  }>;
};

export type ThemeConstantsEntity = {
  fontFamily: string;
  sectionMinHeight: string;
  breakpoints: Record<`${Breakpoints}`, string>;
  typography: Record<`${TypographyVariants}`, TypographyEntity>;
};

export type CommonThemeEntity = {
  black: string;
  white: string;
  gray1: string;
  gray2: string;
  gray3: string;
  gray4: string;
  gray5: string;
  gray6: string;
  gray7: string;
  gray8: string;
  gray9: string;
  info: string;
  active: string;
  warning: string;
  alert: string;
};

export type PaletteThemeEntity = {
  background: string;
  text: string;
  primary: string;
  secondary: string;
  tertiary: string;
  primaryContrast: string;
  secondaryContrast: string;
  tertiaryContrast: string;
};

export type ThemeEntity = PaletteThemeEntity & CommonThemeEntity;

declare module 'styled-components' {
  export interface DefaultTheme {
    colors: ThemeEntity;
    constants: ThemeConstantsEntity;
  }
}
